<template>
      <nav class="navbar navbar-inverse navbar-fixed-top">
         <div class="container-fluid">
             <!-- Brand and toggle get grouped for better mobile display -->
             <div class="navbar-header">
                 <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
                     <span class="sr-only">Toggle navigation</span>
                     <span class="icon-bar"></span>
                     <span class="icon-bar"></span>
                     <span class="icon-bar"></span>
                 </button>
                 <a class="navbar-brand" href="#"><span class="glyphicon glyphicon-copy" aria-hidden="true">PrismCredit</a>
             </div>
            <form id='searchFrom' class="navbar-form navbar-right" role="search">
                <!-- <div class="form-group">
                    <input id='searchInputOne' type="text" class="form-control" placeholder="公司名称...">
                    <input v-if='isMultiSearchMode' id='searchInputTwo' type="text" class="form-control" placeholder="关联公司名称...">
                </div> -->
                <div class="form-group">
                    <select id='searchMode' class="form-control" v-model="selectedStatues.searchMode">
                        <option selected='selected' class="selectPlaceHolder" value="">选择搜索模式</option>
                        <option value="single">单个主体搜索</option>
                        <option value="multi">关联主体搜索</option>
                    </select>
                </div>
                <div class="form-group">
                    <select id='searchDimension' class="form-control" v-model="selectedStatues.searchDimension">
                        <option selected='selected' class="selectPlaceHolder" value="">选择数据维度</option>
                        <option value="one">一度数据</option>
                        <option  value="two">二度数据</option>
                        <option  value="three">三度数据</option>
                        <option  value="four">四度数据</option>
                    </select>
                </div>
                <div class="form-group">
                    <input id='searchInputOne' type="text" class="form-control" placeholder="公司名称...">
                    <input v-if='isMultiSearchMode' id='searchInputTwo' type="text" class="form-control" placeholder="关联公司名称...">
                </div>
                <button id='searchSubmit' type="submit" class="btn btn-default">搜索</button>
            </form>
         </div><!-- /.container-fluid -->
     </nav>

</template>

<script>
export default {
    data () {
        return {
            selectedStatues: {
                searchMode: '',
                searchDimension: ''
            }
        }
    },
    computed: {
        isMultiSearchMode: function() {
            if (this.selectedStatues.searchMode == 'multi') {
                return true;
            } else {
                return false;
            }
        }
    }
}
</script>

<style>
    #searchFrom {
        margin-top: 12px;

    }

    #searchInputOne {
        -webkit-border-radius: 13px;
        border-radius: 13px;
        height: 26px;
        margin-left: 10px;
        margin-right: 10px;
        font-size: 12px;
    }

    #searchInputTwo {
        -webkit-border-radius: 13px;
        border-radius: 13px;
        height: 26px;
        /*margin-left: 10px;*/
        margin-right: 10px;
        font-size: 12px;
    }

    #searchDimension {
        height: 26px;
        font-size: 12px;
        padding-top: 0;
        padding-bottom: 0;
        width: 120px;
        background-color: #333;
        color: #fff;
        border: 0;
    }

    #searchMode {
        height: 26px;
        font-size: 12px;
        padding-top: 0;
        padding-bottom: 0;
        width: 120px;
        background-color: #333;
        color: #fff;
        border: 0;
    }

    #searchSubmit {
        height: 26px;
        font-size: 12px;
        padding-top: 0;
        padding-bottom: 0;
        background-color: steelblue;
        border: 0;
        color: white;
    }

    /*隐藏select展开时候的占位提示文字*/
    .selectPlaceHolder {
        display:none;
    }

    /*.searchComponet {
        height: 26px;
        font-size: 12px;
        padding-top: 0;
        padding-bottom: 0;
    }*/

</style>



